<template>
 <div>  
<div id="CreatePostDialog"> 
         
 <DialogBoxModal :class="'object dialogboxmodal0 '"   id="dialogboxmodal0" ref="dialogboxmodal0"  v-bind:style="{}" :close = "close" 
 :action = "post" 
 :title = "$t('Feed.startPost')" 
 :hasAction = "true" 
 :actionName = "$t('Feed.post')" 
 :maxWidth = "500" 
>
 <template  v-slot:content>
