 :_minWidth = "'unset'" 
  v-bind:style="{flex : '1',minWidth : 'unset',}"><v-icon  :style = "{}"  v-if = "'ion-ios-image' !== undefined"  :left = "true" 
 :color = "'#378fe9'" 
