 <input  type = "text" :class="'textedit texteditsearchZone3 '"   id="texteditsearchZone3" ref="texteditsearchZone3" v-model="filter" :placeholder = "$t('Blog.search')" 
 :outline = "'none'" 
  v-bind:style="{color : '#000',}" >  </div>  </div>  </div>
