/**
 * Please don' t remove this.
 * Artefact will use it to inject code
 */
//<= inject_package =>
  
import AOS from 'aos'; 
import 'aos/dist/aos.css'
//<= end_package =>


window.$ = require('jquery');
window.JQuery = require('jquery')

Vue.config.productionTip = false

Vue.use(VueMeta)

let options = {}

Vue.use(VueAWN, options)

Vue.use(VueMq, {
  breakpoints: {
    xs: 600,
    sm: 960,
    md: 1264,
    lg: 1904,
    xl: 2204,
    default: Infinity,
  }
})


Vue.mixin({
  methods: {
    mqVal: function (obj, vp) {
      if (vp == "default") {
        return (obj.default ?? this.mqVal(obj, "xl"))
      } else if (vp == "xs") {
        return (obj.xs ?? obj.default)
      } else if (vp == "sm") {
        return (obj.sm ?? obj.default)
      } else if (vp == "md") {
        return (obj.md ?? this.mqVal(obj, "sm"))
      } else if (vp == "lg") {
        return (obj.lg ?? this.mqVal(obj, "md"))
      } else if (vp == "xl") {
        return (obj.xl ?? this.mqVal(obj, "lg"))
      }
    },

    getUnit: function (value, unit) {
      if(typeof value === 'string') {
        return value + (value.match(/\d+[^\d]+/)? '' : unit)
      } else {
        return value + unit
      }
    }
  },
})

window.addEventListener("load", () => {
  //<= inject_main_loaded =>

		document.querySelector('.scrollarea').addEventListener('scroll', () => { AOS.refresh(); })
//<= end_loaded =>
});

new Vue({
  created() {
    //<= inject_main_created =>
AOS.init()
//<= end_created =>
  },

  router,
  store,
  vuetify,
  i18n,
//apolloProvider: createProvider(),
  render: h => h(App)
}).$mount('#app')
