<template>
 <div>  
<div id="SiteBuilder"> 
         
 <div  :class="'row builderRow '"   id="builderRow" ref="builderRow"  v-bind:style="{backgroundColor : '#eee',}">
 <div  :class="'column leftPanel '"   id="leftPanel" ref="leftPanel"  v-bind:style="{backgroundColor : '#1d252b',}">
 <v-btn tag="div" 
 :color = "primaryColor" 
 :text = "true" 
 :dark = "true" 
 @click="addPage" :class="'button buttonleftPanel1 '"   id="buttonleftPanel1" ref="buttonleftPanel1"  v-bind:style="{}"><span>{{ 'Add new page' }}</span>
</v-btn>
 <div  :class="'scrollarea scrollarealeftPanel3 '"   id="scrollarealeftPanel3" ref="scrollarealeftPanel3" :_flex = "'1'" 
  v-bind:style="{flex : '1',backgroundColor : 'transparent',backgroundColor : 'transparent',}">
 <div  :class="'column pageList '"   id="pageList" ref="pageList"  v-bind:style="{}">
 <div  :class="'row rowpageList3 '"   id="rowpageList3" ref="rowpageList3" v-for="(page, pageIndex) in pages" :key="'rowpageList3' + pageIndex"  v-bind:style="{backgroundColor : isSelected(page) ? primaryColor : '#444',}">
 <div  :class="'text textrowpageList31 '"   id="textrowpageList31" ref="textrowpageList31" :_flex = "'1'" 
  v-bind:style="{flex : '1',color : isSelected(page) ? '#fff' : 'gray',}">
<div class="text-content" :style = "{}">{{ page.get('name') || page.id }}</div>

   </div>
 <div  :class="'mousearea selectArea '"   id="selectArea" ref="selectArea" v-ripple = "true"  v-bind:style="{}" @click="selectPage(page.id)"></div>
 <v-btn tag="div" 
 :text = "true" 
 @click="removePage(page.id)" :class="'button buttonrowpageList33 '"   id="buttonrowpageList33" ref="buttonrowpageList33" v-if="!isSelected(page)"  v-bind:style="{}"><span></span>
<v-icon  :style = "{}"  v-if = "'ion-ios-close' !== undefined" >{{ 'ion-ios-close' }}
</v-icon></v-btn>  </div>  </div>  </div>  </div>
 <div  :class="'item gjs '"   id="gjs" ref="gjs" :_flex = "'1'" 
  v-bind:style="{flex : '1',}">  </div>  </div>
 
      </div>
 </div> 
</template>
<script>

import grapesjs from "grapesjs";
import "grapesjs/dist/css/grapes.min.css";

import gjsPresetWebpage from "grapesjs-preset-webpage";
import gjsBlocksBasic from 'grapesjs-blocks-basic';
import gjsForms from 'grapesjs-plugin-forms';
import gjsCKEditor from 'grapesjs-plugin-ckeditor';
import gjsExport from 'grapesjs-plugin-export';
import gjsTabs from 'grapesjs-tabs';
import gjsCountdown from 'grapesjs-component-countdown';
import gjsCustomCode from 'grapesjs-custom-code';
import gjsNavBar from 'grapesjs-navbar';
import gjsToolTip from 'grapesjs-tooltip';
import gjsParserPostCss from 'grapesjs-parser-postcss';
import gjsTyped from 'grapesjs-typed';
import gjsFlexBlocs from 'grapesjs-blocks-flexbox';
import gjsTailwind from 'grapesjs-tailwind';


import gjsComponentCodeEditor from 'grapesjs-component-code-editor';
import 'grapesjs-component-code-editor/dist/grapesjs-component-code-editor.min.css';

import gjsStyleBg from 'grapesjs-style-bg';
import "grapick/dist/grapick.min.css";
export default {
	name: 'SiteBuilder',

    components: {},
	methods: {
	
		loadProjectData: function(val) { 		
	this.projectData = val

}
,
		loadData: function() { 		
	if(this.editor){	

		if(this.projectData){

			this.editor.loadProjectData(this.projectData);

		}

	}

}
,
		saveData: function(editor) { 		
	let projectData = this.editor.getProjectData();

	this.returnData(projectData)

}
,
		setPages: function(pages) {		
  this.pages = [...pages];

}
,
		isSelected: function(page) {		
  return this.pm.getSelected().id == page.id;

}
,
		selectPage: function(pageId) {		
  return this.pm.select(pageId);

}
,
		removePage: function(pageId) {		
  return this.pm.remove(pageId);

}
,
		addPage: function() {		
  const { pm } = this;

  const len = pm.getAll().length;

  pm.add({

    name: `Page ${len + 1}`,

    component: '<div>New page</div>',

  });

}
,
	},
	mounted(){
			
	const escapeName = (name) => `${name}`.trim().replace(/([^a-z0-9\w-:/]+)/gi, '-');

	

	this.editor = grapesjs.init({

     container: '#gjs',

     height: '100%',

     width: '100%',		

		  showOffsets: true,

		  fromElement: true,

		  noticeOnUnload: false,

			selectorManager: { escapeName },

			pageManager: {		

		    pages: [{

		      id: 'page-1',

		      name: 'Page 1',

		      component: '<div id="comp1">Page 1</div>',

		      styles: `#comp1 { color: red }`,

		    }]

		  },

     plugins: [

				gjsPresetWebpage,

				gjsBlocksBasic,

				gjsForms,

				//gjsCKEditor,

				gjsExport,

				gjsTabs,

				gjsCountdown,

				gjsCustomCode,

				gjsNavBar,

				gjsToolTip,

				gjsParserPostCss,

				gjsStyleBg,

				gjsTyped,

				gjsFlexBlocs,

				gjsTailwind,

				gjsComponentCodeEditor,

				//gjsBootstrap4, 

			],

     storageManager: false,

			commands: {		

		    defaults: [

		      {

		        id: 'store-data',

		        run(editor) {

		          editor.store();

		        },

		      }

		    ]

		  },

     deviceManager: {

       devices:

       [

         {

           id: 'desktop',

           name: 'Desktop',

           width: '',

         },

         {

           id: 'tablet',

           name: 'Tablet',

           width: '768px',

           widthMedia: '992px',

         },

         {

           id: 'mobilePortrait',

           name: 'Mobile portrait',

           width: '320px',

           widthMedia: '575px',

         },

       ]

     },

     pluginsOpts: {},

			canvas: {

        styles: [

          'https://stackpath.bootstrapcdn.com/bootstrap/4.1.3/css/bootstrap.min.css'

        ],

        scripts: [

          'https://code.jquery.com/jquery-3.3.1.slim.min.js',

          'https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.14.3/umd/popper.min.js',

          'https://stackpath.bootstrapcdn.com/bootstrap/4.1.3/js/bootstrap.min.js'

        ],

      }

   })

	

	const { pm } = this;

  this.setPages(pm.getAll());

  this.editor.on('page', () => {

    this.pages = [...pm.getAll()];

  });

	

	const commands = this.editor.Commands;

	commands.add('saveData', this.saveData);

	commands.add('goBack', this.goBack);

	

	const panelManager = this.editor.Panels;

	

	const panelViews = panelManager.addPanel({	

	  id: 'views'

	});

	panelViews.get('buttons').add([{

	  attributes: {

	     title: 'Open Code'

	  },

	  className: 'mdi mdi-code-braces',

	  command: 'open-code',

	  togglable: true, //close when button is clicked again

	  id: 'open-code'

	}]);

	

	var saveBtn = panelManager.addButton('options',{

  	id: 'saveBtn',

	  className: 'mdi mdi-content-save',

	  command: 'saveData',

	});

	

	var backBtn = panelManager.addButton('devices-c',{

  	id: 'backBtn',

	  className: 'mdi mdi-arrow-left',

	  command: 'goBack',

	});

	

	var themeBtn = panelManager.addButton("options", {	

	  id: "update-theme",

	  className: "mdi mdi-palette",

	  command: "open-update-theme",

	  attributes: {

	    title: "Update Theme",

	    "data-tooltip-pos": "bottom"

	  }

	});

	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		pm: function() { 		
	return this.editor.Pages;

}
,
	},
	watch: {
	
		editor: function(val) { 		
	this.loadData()

}
,
		projectData: function(val) { 		
	this.loadData()

}
,
	},
	data(){
	return {
		
		projectData:  null		,
		editor:  null		,
		pages:  []		,
		}
	},
	props: {
	
		returnData: {			type: Function,			required: false,			default: () =>  (_ => {}),		},
		goBack: {			type: Function,			required: false,			default: () =>  (_ => {}),		},
	},
} 
</script>

