<script>
import MainLayout from '../../At/Layouts/MainLayout'
import ArticleBox from '../../At/Components/BlogPage/ArticleBox'
import ArticleBoxMini from '../../At/Components/BlogPage/ArticleBoxMini'

import * as Request from '../../Js/requests'
export default {
	name: 'Blog',

    components: {MainLayout,ArticleBox,ArticleBoxMini,},
	methods: {
	
		getArticles: function() {		
	let config = {

		pagination: {

			page: this.curPage,

			pageSize: this.artiPerPage,

			withCount: true

		},

		sort: ['id:desc']

	}

	

	Request.getLocalTableArgs('articles', config)

	.then((res) => {

		this.articles = res.data.data;

		

		let pagination = res.data.meta.pagination;

		this.numberPages = pagination.pageCount

		

		this.loading = false;

	}) 

}
,
	},
	mounted(){
			
	this.getArticles()

	},
	metaInfo(){
			
	return {

		title: this.companyName + " | Blog",

		meta: [		

	   { name: 'description', content: this.$t('Blog.description')},

	

	   { property: 'og:title', content: this.companyName + " | Blog"},

	   { property: 'og:description', content: this.$t('Blog.description')},

	   { property: 'og:type', content: 'blog'},

	   { property: 'og:url', content: this.$store.state.webUrl + '/blog'},

	

	   { name: 'twitter:title', content: this.companyName + " | Blog"},

	   { name: 'twitter:description', content: this.$t('Blog.description')},

	   { name: 'twitter:card', content: 'blog'},

	   { name: 'twitter:url', content: this.$store.state.webUrl + '/blog'}

	  ]

	}

	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		selectedLang: function() { 		
	return this.$store.state.selectedLang

}
,
		filtredArti: function(){ 		
	if(this.filter){

		return this.articles.filter(y => {

			let title = y.title.toLowerCase();

			let description = y.description.toLowerCase();

			let filter = this.filter.toLowerCase()

			

			return title.includes(filter)

						|| description.includes(filter)

		})

	} else {

		return this.articles

	}

}
,
		foundNumber: function() { 		
		return '(' + this.filtredArti.length + ')'

}
,
		companyName: function() { 		
	return this.$store.state.companyName

}
,
	},
	watch: {
	
		curPage: function(val) { 		
	this.getArticles()

}
,
		selectedLang: function() { 		
	this.getArticles()

}
,
	},
	data(){
	return {
		
		articles:  []		,
		filter:  null		,
		loading:  true		,
		artiPerPage:  24		,
		curPage:  1		,
		numberPages:  0		,
		}
	},
} 
</script>

