<script>
import AdminMediaPicker from '../../../../At/Components/AdminPage/AdminMediaPicker'

import * as Main from '../../../../Js/main'
import * as Request from '../../../../Js/requests'

import { VueEditor } from "vue2-editor";
export default {
	name: 'AdminOrganisationForm',

    components: {AdminMediaPicker,VueEditor,},
	methods: {
	
		loadData: function() { 		
	this.name = this.entity?.name

	this.title = this.entity?.title

	this.slug = this.entity?.slug

	this.email = this.entity?.email

	this.keyWords = this.entity?.keyWords

	this.websiteLink = this.entity?.websiteLink

	this.phoneNumber = this.entity?.phoneNumber

	this.field = this.entity?.field

	this.creationDate = this.entity?.creationDate

	this.location = this.entity?.location

	this.gpsLocation = this.entity?.gpsLocation

	this.description = this.entity?.description

	this.goal = this.entity?.goal

	

	this.logo = this.entity?.logo.data

	this.bannerImage = this.entity?.bannerImage.data

	

	this.loading = false

}
,
		generateSlug: function() { 		
	if(this.title){

		this.slug = this.title.replaceAll(/\s+/g, '-')

	}

}
,
		setLogo: function(val) { 		
	this.m_logo = val

}
,
		setBannerImge: function(val) { 		
	this.m_bannerImage = val

}
,
		chargeDate: function() { 		
	let data = {}

	

	if(this.name){ data.name = this.name }

	if(this.title){ data.title = this.title }

	if(this.slug){ data.slug = this.slug }

	if(this.description){ data.description = this.description }

	if(this.goal){ data.goal = this.goal }

	if(this.email){ data.email = this.email }

	if(this.keyWords){ data.keyWords = this.keyWords }

	if(this.websiteLink){ data.websiteLink = this.websiteLink }

	if(this.phoneNumber){ data.phoneNumber = this.phoneNumber }

	if(this.field){ data.field = this.field }

	if(this.creationDate){ data.creationDate = this.creationDate }

	if(this.location){ data.location = this.location }

	if(this.gpsLocation){ data.gpsLocation = this.gpsLocation }

	

	return data

}
,
		createEntry: function() { 		
	let data = this.chargeDate()

	data.president = this.$store.state.user.id

	

	data.publishedAt = new Date().toISOString()

	

  const formData = new FormData();

	

	if(this.localization == 'fr'){ 	

		data.redactor = this.$store.state.user.id

			

		if(this.m_logo){	

			formData.append(`files.logo`, this.m_logo, this.m_logo.name);

		}

		

		if(this.m_bannerImage){

			formData.append(`files.bannerImage`, this.m_bannerImage, this.m_bannerImage.name);

		}

	} else {

		data.locale = this.localization

	}

	

	formData.append('data', JSON.stringify(data));

	

	Request.createLocalizedEntry(this.tableName, this.entity?.id ,formData)

	.then(res => {

		if(res.data.error){

			this.showError(

				res.data.error.details.errors[0].path.toString()

				+ ' ' + res.data.error.details.errors[0].message

			)

		} else {	

			this.$awn.success(this.$t('Admin.entryCreated')) 

			this.updateSelectedItem(res.data.data.id)

		}

	})

	.catch(error => {

		this.showError(

			error.response.data.error.message

			+ ' : ' + error.response.data.error.details.errors[0].path.toString()

		)

	})

}
,
		updateEntry: function() { 		
	let data = this.chargeDate()

	

	if(this.localization == 'fr'){ 	

			

		if(this.m_logo){	

			data.logo = this.m_logo

		}

		

		if(this.m_bannerImage){

			data.bannerImage = this.m_bannerImage;

		}

	}

	

	Request.updateEntry(this.tableName, this.subEntity.id, data)

	.then(res => {

		if(res.data.error){

			this.showError(

				res.data.error.details.errors[0].path.toString()

				+ ' ' + res.data.error.details.errors[0].message

			)

		} else {	

			this.$awn.success(this.$t('Admin.entryUpdated'))

			this.updateSelectedItem(res.data.data.id)

		}

	})

	.catch(error => {

		console.log(error)

		this.showError(

			error.response.data.error.message

			+ ' : ' + error.response.data.error.details.errors[0].path.toString()

		)

	})

}
,
		showError: function(message) { 		
	this.error = message

	this.errorVisible = true	

	this.$awn.alert(message)

}
,
	},
	mounted(){
			
	this.loadData()

	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		entityID: function() { 		
	return this.entity?.id

}
,
	},
	watch: {
	
		subEntity: function(val) { 		
	this.loading = true

	this.changed = false

	this.loadData()

}
,
		name: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		title: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		slug: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		email: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		keyWords: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		websiteLink: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		phoneNumber: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		field: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		creationDate: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		location: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		gpsLocation: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		m_logo: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		m_bannerImage: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		description: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		goal: function(val) { 		
	if(!this.loading) this.changed = true

}
,
		changed: function(val) { 		
	this.changed = val

}
,
	},
	data(){
	return {
		
		name:  null		,
		slug:  null		,
		title:  null		,
		email:  null		,
		keyWords:  null		,
		websiteLink:  null		,
		phoneNumber:  null		,
		field:  null		,
		creationDate:  null		,
		location:  null		,
		gpsLocation:  null		,
		description:  null		,
		goal:  null		,
		logo:  null		,
		bannerImage:  null		,
		m_logo:  null		,
		m_bannerImage:  null		,
		loading:  true		,
		changed:  false		,
		error:  null		,
		errorVisible:  false		,
		tableName:  'articles'		,
		}
	},
	props: {
	
		entity: {					required: false,			default: () =>  null,		},
		subEntity: {					required: false,			default: () =>  null,		},
		localization: {					required: false,			default: () =>  null,		},
		updateSelectedItem: {			type: Function,			required: false,			default: () =>  (_ => {}),		},
	},
} 
</script>

