<template>
 <div>  
<div id="ExtendableRichTextSection"> 
         
 <div  :class="'column columncolumn032 '"   id="columncolumn032" ref="columncolumn032"  v-bind:style="{}">
 <div  :class="'text textrowcolumncolumn01331 '"   id="textrowcolumncolumn01331" ref="textrowcolumncolumn01331" v-if="hasTitle"  v-bind:style="{}">
<div class="text-content" :style = "{}">{{ title }}</div>

   </div>
 <RichText :class="'object textContentRich '"   id="textContentRich" ref="textContentRich"  v-bind:style="{height : getUnit(m_heigth,'px'),}" :value = "text" 
>
</RichText>
 <v-btn tag="div" 
 :color = "primaryColor" 
 :text = "true" 
 @click="togleExtend" :class="'button buttonrowcolumncolumn013373 '"   id="buttonrowcolumncolumn013373" ref="buttonrowcolumncolumn013373" v-if="longText"  v-bind:style="{}"><span>{{ extended ? 'Afficher moins' : 'Afficher plus' }}</span>
<v-icon  :style = "{}"  v-if = "extended ? 'ion-ios-arrow-up' : 'ion-ios-arrow-down' !== undefined" >{{ extended ? 'ion-ios-arrow-up' : 'ion-ios-arrow-down' }}
</v-icon></v-btn>  </div>
 
      </div>
 </div> 
</template>
<script>
import RichText from '../../At/Utils/RichText'


export default {
	name: 'ExtendableRichTextSection',

    components: {RichText,},
	methods: {
	
		togleExtend: function() { 		
	this.extended = !this.extended

}
,
	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		longText: function() { 		
	return this.text.length > this.maxCharacters

}
,
		m_heigth: function() { 		
	return (!this.extended ? "auto" : this.maxCharacters)

}
,
		m_text: function() { 			
	if(this.text){

		if(this.longText){	

			if(this.extended){

				return this.text

			}

			return this.text.slice(0, this.maxCharacters) + '...'

		} else {

			return this.text

		}

	}

	return "..."

}
,
	},
	data(){
	return {
		
		extended:  false		,
		}
	},
	props: {
	
		title: {					required: false,			default: () =>  "Description du cours",		},
		text: {					required: false,			default: () =>  "...",		},
		maxCharacters: {					required: false,			default: () =>  500,		},
		hasTitle: {			type: Boolean,			required: false,			default: () =>  true,		},
	},
} 
</script>

