<template>
 <div>  
<div id="WebinarDetail"> 
         
 <DetailsPage :class="'object detailspage0 '"   id="detailspage0" ref="detailspage0"  v-bind:style="{}" :info = "info" 
 :isWebinar = "true" 
 :currentTab = "2" 
 :slug = "slug" 
>
</DetailsPage>
 
      </div>
 </div> 
