<script>
import DetailsPage from '../../At/Sections/DetailsPage'

import * as Request from '../../Js/requests'
export default {
	name: 'WebinarDetail',

    components: {DetailsPage,},
	mounted(){
			
	let config = {	

	  filters: {

	    slug: {

	      $eq: this.slug,

	    },

	  },

	}

	

	Request.getLocalTableArgs('webinars', config)

	.then((res) => {

		this.info = res.data.data[0]

	})

	},
	computed: {
	
		slug: function() { 		
	return this.$route.params.slug

}
,
	},
	watch: {
	
		slug: function(val) { 		
	location.reload();

}
,
	},
	data(){
	return {
		
		info:  null		,
		}
	},
} 
</script>

