 :outlined = "true" 
 @click="print" :class="'button printBtn '"   id="printBtn" ref="printBtn"  v-bind:style="{}"><v-icon  :style = "{}"  v-if = "'ion-md-print' !== undefined"  :left = "true" 
>{{ 'ion-md-print' }}
</v-icon><span>{{ 'PRINT' }}</span>
</v-btn>  </div>
