<script>
import MainLayout from '../../At/Layouts/MainLayout'
import ContentWrapper from '../../At/Utils/ContentWrapper'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
export default {
	name: 'Login',

    components: {MainLayout,ContentWrapper,},
	methods: {
	
		toggelPass: function() { 		
	this.showPass = !this.showPass

}
,
		loginGoogle: function() {		
	Request.signinGoogle()

}
,
		loginFacebook: function() {		
	Request.signinFacebook()

}
,
		loginLinkedin: function() {		
	Request.signinLinkedin()

}
,
		showError: function(message) { 		
	this.error = message

	this.errorVisible = true	

}
,
		login: function() { 		
	// check email

	if(!Main.validateEmail(this.email)){

		this.showError(this.$t('Landing.provideGoodMail'))	

		return

	}

	// check pass

	if(this.pass == null){

		this.showError(this.$t('Signup.enterPass'))		

		return

	}	

	

	Request.login(this.email, this.pass)

	.then(res => {

		let {user, jwt} = res.data

		

		this.$store.commit('changeToken', jwt)

			

		let token = this.$route.params.token

		if(token == "successful"){

			Main.autoErollment(

				user.id,

				_ => {

					this.$store.commit('changeUser', user)

					this.$router.push('/')

				}

			)

		} else {

			this.$store.commit('changeUser', user)

			this.$router.push('/')

		}

	})

	.catch(error => {

		this.showError(error)

	})

}
,
	},
	mounted(){
			
	if(this.$store.state.user){

		this.$router.push('/')

	}

	},
	metaInfo(){
			
	return {

		title: this.companyName + " | Login",

		meta: [		

	   { name: 'description', content: this.$t('LogIn.description')},

	

	   { property: 'og:title', content: this.companyName + " | Login"},

	   { property: 'og:description', content: this.$t('LogIn.description')},

	   { property: 'og:type', content: 'website'},

	   { property: 'og:url', content: this.$store.state.webUrl + '/login'},

	

	   { name: 'twitter:title', content: this.companyName + " | Login"},

	   { name: 'twitter:description', content: this.$t('LogIn.description')},

	   { name: 'twitter:card', content: 'website'},

	   { name: 'twitter:url', content: this.$store.state.webUrl + '/login'}

	  ]

	}

	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		passIcon: function(){		
	if(this.showPass){

		return 'ion-ios-eye'

	} else {

		return 'ion-ios-eye-off'

	}

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		companyName: function() { 		
	return this.$store.state.companyName

}
,
	},
	data(){
	return {
		
		email:  null		,
		pass:  null		,
		errorVisible:  false		,
		error:  "..."		,
		showPass:  false		,
		}
	},
} 
</script>

