<template>
 <div>  
<div id="ExtendableTagSection"> 
         
 <div  :class="'column columncolumn032 '"   id="columncolumn032" ref="columncolumn032"  v-bind:style="{}">
 <div  :class="'text textrowcolumncolumn01331 '"   id="textrowcolumncolumn01331" ref="textrowcolumncolumn01331"  v-bind:style="{}">
<div class="text-content" :style = "{}">{{ title }}</div>

   </div>
 <div  :class="'flow flowcolumncolumn0325 '"   id="flowcolumncolumn0325" ref="flowcolumncolumn0325"  v-bind:style="{}">
 <v-btn tag="div" 
 :color = "'#7c7c7c'" 
 :outlined = "true" 
 :rounded = "true" 
 :class="'button buttonflowcolumncolumn03251 '"   id="buttonflowcolumncolumn03251" ref="buttonflowcolumncolumn03251" v-for="(item,index) in m_list" :key="'buttonflowcolumncolumn03251' + index"  v-bind:style="{}"><span>{{ item.name }}</span>
</v-btn>  </div>
 <v-btn tag="div" 
 :color = "primaryColor" 
 :text = "true" 
 @click="togleExtend" :class="'button buttonrowcolumncolumn013373 '"   id="buttonrowcolumncolumn013373" ref="buttonrowcolumncolumn013373" v-if="longList"  v-bind:style="{}"><span>{{ extended ? 'Afficher moins' : 'Afficher plus' }}</span>
<v-icon  :style = "{}"  v-if = "extended ? 'ion-ios-arrow-up' : 'ion-ios-arrow-down' !== undefined" >{{ extended ? 'ion-ios-arrow-up' : 'ion-ios-arrow-down' }}
</v-icon></v-btn>  </div>
 
      </div>
 </div> 
</template>
<script>


export default {
	name: 'ExtendableTagSection',

    components: {},
	methods: {
	
		togleExtend: function() { 		
	this.extended = !this.extended

}
,
	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		longList: function() { 		
	return this.list.length > this.maxItems

}
,
		m_list: function() { 			
	if(this.list){

		if(this.longList){	

			if(this.extended){

				return this.list

			}

			return this.list.slice(0, this.maxItems)

		} else {

			return this.list

		}

	}

	return []

}
,
	},
	data(){
	return {
		
		extended:  false		,
		}
	},
	props: {
	
		title: {					required: false,			default: () =>  "Description du cours",		},
		list: {					required: false,			default: () =>  [0, 0, 0, 0, 0, 0, 0],		},
		maxItems: {					required: false,			default: () =>  5,		},
	},
} 
</script>

